$color-primary;\n  border: $button-border-width solid $color-primary;\n  border-radius: $border-radius;\n  color: $color-white;\n  cursor: pointer;\n  display: inline-block;\n  font-size: $font-size-default;\n  height: $space-two * 2;\n  line-height: $line-height;\n  outline: none;\n  padding: $space-smaller $space-normal;\n  text-align: center;\n  text-decoration: none;\n  transition: background .2s, border .2s, box-shadow .2s, color .2s;\n  user-select: none;\n  vertical-align: middle;\n  white-space: nowrap;\n\n  &:focus,\n  &:hover {\n    background: lighten($color-primary, 7%);\n    border-color: $color-primary;\n    text-decoration: none;\n  }\n\n  &:active,\n  &.active {\n    background: $color-primary;\n    border-color: darken($color-primary, 5%);\n    color: lighten($color-primary, 20%);\n    text-decoration: none;\n  }\n\n  &[disabled],\n  &:disabled,\n  &.disabled {\n    cursor: default;\n    opacity: .5;\n    pointer-events: none;\n  }\n\n  &.small {\n    font-size: $font-size-small;\n    height: $space-medium;\n    padding: $space-smaller $space-slab;\n  }\n\n  &.large {\n    font-size: $font-size-medium;\n    height: $space-larger;\n    padding: $space-small $space-medium;\n  }\n\n  &.block {\n    width: 100%;\n  }\n\n  &.transparent {\n    background: transparent;\n    border: 0;\n    height: auto;\n  }\n\n  &.compact {\n    padding: 0;\n  }\n}\n","// Font sizes\n$font-size-micro: 0.5rem;\n$font-size-mini: 0.625rem;\n$font-size-small: 0.75rem;\n$font-size-default: 0.875rem;\n$font-size-medium: 1rem;\n$font-size-large: 1.25rem;\n$font-size-big: 1.5rem;\n$font-size-bigger: 2rem;\n$font-size-mega: 2.5rem;\n$font-size-giga: 3.5rem;\n\n// spaces\n$zero: 0;\n$space-micro: 0.125rem;\n$space-smaller: 0.25rem;\n$space-small: 0.5rem;\n$space-one: 0.625rem;\n$space-slab: 0.75rem;\n$space-normal: 1rem;\n$space-two: 1.25rem;\n$space-medium: 1.5rem;\n$space-large: 2rem;\n$space-larger: 3rem;\n$space-big: 4rem;\n$space-jumbo: 5rem;\n$space-mega: 6.25rem;\n\n// font